* {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-style: normal;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  color: #212121;
  font-family: 'DM Sans', sans-serif;
  font-size: 14px;
}

img {
  display: block;
  width: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
    margin: 0 auto;
  }
}
.background-gallery {
  background-image: linear-gradient(to bottom, #fff5f6 0%, #fff5f6 40%, #fff 40%, #fff 100%);
}

.gallery__heading {
  display: none;
}

.reviews {
  width: 100%;
  margin-top: 80px;
}
.reviews__list {
  width: 100%;
}
.reviews__list-item {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.reviews__list-item--hidden {
  display: none;
}
.reviews__list-item::after {
  position: absolute;
  display: block;
  width: 100%;
  height: auto;
  content: '• • •';
  bottom: 73px;
  left: 0;
  color: #c7133e;
  width: 100%;
  height: auto;
  text-align: center;
}
.reviews__heading {
  display: none;
}
.reviews__img {
  width: 26.5%;
  margin-bottom: 29px;
}
.reviews__block {
  position: relative;
  padding: 43px 66px 26px 57px;
  background-color: #fafafa;
  color: #000;
  font-weight: 500;
  font-size: 12px;
  line-height: 255%;
  text-align: center;
  z-index: 1;
  margin-bottom: 52px;
}
.reviews__block::after {
  position: absolute;
  display: block;
  content: '';
  top: 28px;
  left: 61px;
  width: 54px;
  height: 40px;
  background-image: url(/images/quote.png);
  z-index: -1;
}
.reviews__text-block {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.reviews__text-block::after {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  height: 102%;
  top: 0;
  left: 0;
  background-image: repeating-linear-gradient(
    to bottom,
    #fafafa 0px,
    #fafafa 30px,
    #e1e1e1 31px,
    #e1e1e1 31px
  );
  z-index: -1;
}
.reviews__name {
  display: block;
  color: #000;
  font-weight: 500;
  font-size: 14px;
  line-height: 186%;
  text-align: center;
}

.reviews__slider {
  display: block;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 18px;
  margin-top: 30px;
}
.reviews__label {
  position: relative;
  display: block;
  width: 12px;
  height: 12px;
}
.reviews__slider-icon {
  position: absolute;
  top: 0;
  left: 0;
  fill: #d31342;
  opacity: 0;
}
.reviews__input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
  display: block;
  width: 12px;
  height: 12px;
  background-color: #d9d9d9;
  border-radius: 50%;
}
.reviews__input:checked {
  background-color: inherit;
}
.reviews__input:checked + .reviews__slider-icon {
  opacity: 1;
} /*# sourceMappingURL=main.css.map */
